body.black-friday-cerveja.home{
    .nav-gwbar-single-rows{
        display: none !important;
    }
    #bloco-c{
        margin-bottom: 0px !important;
    }
    #wapperBottomTriplo{
        .tpl-center{
            display: flex;
            justify-content: space-evenly;

            .box-banner{
                width: 100%;
                h2{
                    font-family: inherit;
                    font-size: 28px;
                    font-weight: 500;
                    text-transform: uppercase;
                    color: #333;
                    width: 100%;
                    text-align: center;
                    display: block;
                }
            }
        }
    }
    .tpl-center{
        .sidebar{
            display: none;
        }
        .wrapperRight{
            float: unset !important;
            width: 100% !important;

            .showcase{
                position: relative;
                margin-top: 20px;
                .main{
                    @media (min-width: 900px){
                        .sub{
                            border: none;
                            height: 10px;
    
                            .encontrado{
                                display: none;
                            }
                            .resultado-busca-filtro{
                                position: absolute;
                                right: 0px;
                                top: 0px;
                            }
                        }
                    }

                    .prateleira{
                        ul>li{
                            width: 22%;
                        }
                    } 
                }
                .tituloShowcase{
                    border-bottom: 2px solid #ffb600;
                    padding-bottom: 15px;
                    padding-top: 5px;
                    margin-bottom: 15px;
                    clear: both;
                    font-family: inherit;
                    font-size: 28px;
                    font-weight: 500;
                    text-transform: uppercase;
                    color: #333;
                    width: 100%;
                    text-align: center;
                    display: block;
                }
            }
        }
    }
}

.modal-discount-header {
    display: flex;
    justify-content: flex-end;
    font-size: 20px;
    position: relative;
    top: 0px;
    left: 20px;
  }
  
  span.close-modal-discount {
    border-radius: 50%;
    border: none;
    cursor: pointer;
  }
  .modal-discount-blackout{
    position: fixed;
    top: 0px;
    left: 0px;
    background: #00000073;
    width: 100vw;
    height: 100vh;
    z-index: 100;
    cursor: pointer;
  }
  .code_discount{
    position: absolute;
    top: -100px;
  }
  .modal-discount-open {
    position: fixed;
    display: flex;
    flex-direction: column;
    z-index: 120;
    left: calc(50vw - 175px);
    top: calc(50vh - 218px);
  }
  
  .modal-discount-open button{
    background: none;
    border: none;
    padding: 0px;
  }
  
  .modal-discount .modal-discount-open .copy-code{
    position: absolute;
    right: 52px;
    top: 163px;
    background-color: #03ba04;
    border: 0;
    padding: 5px 10px 3px;
    margin-top: 8px;
    font-weight: 400;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: center;
    background-image: none!important;
    color: #fff;
    line-height: 12px;
    font-size: 12px;
    font-family: Teko,sans-serif;
    text-shadow: none!important;
  }

  button.copy-code.copy-code2{
    margin-top: 82.5px !important;
  }
  
  .modal-discount .modal-discount-open .copy-code:hover{
    background-color: #cb9100;
  }
  
  .modal-discount-open .btn-discount img{
    width: 350px;
  }
  
  @media screen and (max-width: 1025px){
    .modal-discount-header {
      left: 0px;
      margin-bottom: 10px;
    }
    .modal-discount-open{
      left: calc(50vw - 150px);
    }
    .modal-discount-open .btn-discount img{
      width: 300px;
    }
    .modal-discount .modal-discount-open .copy-code{
        position: absolute;
    right: 44px;
    top: 154px;
    background-color: #03ba04;
    border: 0;
    padding: 2px 10px 0px;
    margin-top: 8px;
    font-weight: 400;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: center;
    background-image: none!important;
    color: #fff;
    line-height: 12px;
    font-size: 12px;
    font-family: Teko,sans-serif;
    text-shadow: none!important;
    }
    .modal-discount .modal-discount-open .copy-code2{
        margin-top: 72.5px !important;
    }
  }